// pages/index.js
import { useEffect, useState } from "react";
import RoomForm from "../components/RoomForm";
import RoomView from "../components/RoomView";
import styles from "../styles/home/Home.module.css";
import ProtectedRoute from "../components/ProtectedRoute";

function Home({ user }) {
  const [rid, setRid] = useState(null);
  const [inRoom, setInRoom] = useState(false);

  // Fun√ß√£o chamada quando sai da sala (Sidebar chama isso!)
  function handleLeaveRoom() {
    setInRoom(false);
    setRid(null);
  }

  // Disponibiliza a fun√ß√£o global para o _app.js acessar
  useEffect(() => {
    window.__LEAVE_ROOM_CALLBACK__ = () => handleLeaveRoom();
  }, [rid, inRoom]);

  return (
    <div style={{ padding: 24 }}>
      <h1 className={styles.title} style={{ textAlign: "center", marginTop: "-10px" }}>
        Sala de Estimativa
      </h1>

      {!inRoom && (
        <RoomForm
          onEnter={(roomId) => {
            setRid(roomId);
            setInRoom(true);
          }}
        />
      )}

      {inRoom && user && (
        <RoomView
          roomId={rid}
          user={user}
          onLeave={handleLeaveRoom}
        />
      )}
    </div>
  );
}

// Export protegido
export default function ProtectedHome(props) {
  return (
    <ProtectedRoute user={props.user}>
      <Home {...props} />
    </ProtectedRoute>
  );
}